body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "GolosText", sans-serif;
  background-color: #ffffff;
  color: #252525;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  color: #252525;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

.container {
  max-width: 1240px;
  width: 100%;
  padding: 0 24px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
}

.body-scroll-lock {
  overflow: hidden;
}

/* Marquee */
.marquee {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  width: 100%;
  height: 40px;
  background-color: #151515;
  font-size: 13px;
  line-height: 110%;
  font-weight: 500;
  color: #ffffff;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}
.marquee.active {
  opacity: 1;
  pointer-events: auto;
}
.marquee .marquee-fixed {
  display: flex;
  align-items: center;
}
.marquee .marquee-fixed span {
  padding-left: 110px;
}
.marquee__container {
  display: flex;
  align-items: center;
  overflow: hidden;
}

/* Swiper pagination */
.slider-nav__pagi-item {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: rgba(1, 63, 126, 0.2);
  cursor: pointer;
  transition: background-color 0.3s ease;
}
@media only screen and (max-width: 1079px) {
  .slider-nav__pagi-item {
    background-color: #6b6b6b;
  }
}
@media (min-width: 1080px) {
  .slider-nav__pagi-item:hover, .slider-nav__pagi-item:focus {
    background-color: #01488a;
  }
}
@media only screen and (min-width: 1080px) and (max-width: 1079px) {
  .slider-nav__pagi-item:hover, .slider-nav__pagi-item:focus {
    background-color: #01488a;
  }
}
.slider-nav__pagi-item + .slider-nav__pagi-item {
  margin-left: 12px;
}
.slider-nav__pagi-item-active {
  background-color: #01488a;
}
@media only screen and (max-width: 1079px) {
  .slider-nav__pagi-item-active {
    background-color: #ffffff;
  }
}

/* Title */
.title {
  font-size: 35px;
  line-height: 42px;
  font-weight: 500;
  text-align: center;
}
@media only screen and (max-width: 767px) {
  .title {
    font-size: 26px;
    line-height: 30px;
  }
}
.title--left {
  text-align: left;
}
.title--white {
  color: #ffffff;
}

/* International Telephone Input */
.iti {
  width: 100%;
}

.iti__flag {
  background-image: url("../images/flags.png");
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .iti__flag {
    background-image: url("../images/flags@2x.png");
  }
}
.iti--separate-dial-code .iti__selected-flag {
  background-color: #ffffff;
}
.iti--separate-dial-code .iti__selected-dial-code {
  order: 1;
}

.iti--container {
  width: 100%;
  height: 190px;
  position: absolute !important;
  top: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  right: unset !important;
}

/* Field error message */
.field-error {
  display: none;
  position: absolute;
  bottom: -23px;
  left: 9px;
  font-size: 14px;
  color: red;
}

/* Partials */
.logo {
  font-size: 24px;
  line-height: 140%;
  color: #01488a;
}
.logo--light {
  color: #ffffff;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20.5px;
  background-color: #01488a;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s ease;
}
@media (min-width: 1080px) {
  .button:hover, .button:focus {
    background-color: rgba(1, 72, 138, 0.8);
  }
}

.field {
  position: relative;
  display: block;
  width: 100%;
  cursor: text;
}
.field:not(.field--phone) .field__hint {
  position: absolute;
  top: -18px;
  left: 0;
  display: block;
  transition: 0.3s;
  font-size: 12px;
  line-height: 16px;
  color: #000000;
}
.field:not(.field--phone) input::placeholder {
  color: transparent;
}
.field:not(.field--phone) input:placeholder-shown ~ .field__hint {
  font-size: 16px;
  line-height: 16px;
  color: #6b6b6b;
  top: 10px;
}
@media only screen and (max-width: 767px) {
  .field:not(.field--phone) input:placeholder-shown ~ .field__hint {
    font-size: 14px;
    top: 5px;
  }
}
.field:not(.field--phone) input:focus ~ .field__hint {
  font-size: 12px;
  top: -18px;
}
.field input {
  width: 100%;
  height: 43px;
  border: none;
  border-bottom: 1px solid #e6e6e6;
  font-family: "GolosText", sans-serif;
  font-size: 16px;
  line-height: 180%;
  color: #6b6b6b;
  transition: border-bottom-color 0.2s ease;
}
@media only screen and (max-width: 767px) {
  .field input {
    height: 30px;
    font-size: 14px;
  }
}
.field input:focus {
  border-bottom-color: #000000;
}
.field--phone input::placeholder {
  color: #6b6b6b;
}

.header {
  width: 100%;
  padding: 24px 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
.header.open .header__burger::before {
  transform: rotate(45deg) translate(5px, 0);
}
@media only screen and (max-width: 1279px) {
  .header.open .header__burger::before {
    transform: rotate(45deg) translate(0, 5px);
  }
}
.header.open .header__burger::after {
  transform: rotate(-45deg) translate(-5px, 0);
}
@media only screen and (max-width: 1279px) {
  .header.open .header__burger::after {
    transform: rotate(-45deg) translate(-3px, -4px);
  }
}
.header.open .header__mobile {
  transform: translateX(0);
}
.header__container, .header__col, .header__nav {
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 767px) {
  .header__nav, .header__phone {
    display: none;
  }
}
.header__container {
  justify-content: space-between;
}
.header__burger-wrapper {
  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-grow: 1;
  cursor: pointer;
}
@media only screen and (max-width: 1279px) {
  .header__burger-wrapper {
    width: auto;
    height: 100%;
    flex-grow: unset;
  }
}
@media only screen and (max-width: 767px) {
  .header__burger-wrapper {
    display: flex;
  }
}
.header__burger {
  display: flex;
  justify-content: space-between;
  width: 13px;
  height: 57px;
}
@media only screen and (max-width: 1279px) {
  .header__burger {
    flex-direction: column;
    width: 41px;
    height: 7px;
  }
}
.header__burger::before, .header__burger::after {
  content: "";
  display: block;
  width: 4px;
  height: 100%;
  background-color: #252525;
  transition: transform 0.3s ease;
}
@media only screen and (max-width: 1279px) {
  .header__burger::before, .header__burger::after {
    width: 100%;
    height: 2px;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav {
    flex-direction: column;
    margin-top: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .header__nav li {
    text-align: center;
  }
}
.header__nav li + li {
  margin-left: 42px;
}
@media only screen and (max-width: 767px) {
  .header__nav li + li {
    margin-left: 0;
    margin-top: 10px;
  }
}
.header__nav li a {
  display: inline-block;
  border-bottom: 1px solid transparent;
  font-family: "Gilroy", sans-serif;
  font-size: 14px;
  line-height: 140%;
  color: #6b6b6b;
  transition: color 0.3s ease, border-color 0.3s ease;
}
@media (min-width: 1080px) {
  .header__nav li a:hover, .header__nav li a:focus {
    color: #151515;
    border-color: #151515;
  }
}
@media only screen and (max-width: 767px) {
  .header__phone {
    margin-top: 20px;
  }
}
.header__phone a {
  font-size: 16px;
  line-height: 140%;
  color: #ffffff;
}
@media only screen and (max-width: 1079px) {
  .header__phone a {
    color: #252525;
  }
}
@media (min-width: 1080px) {
  .header__phone a:hover, .header__phone a:focus {
    text-decoration: underline;
  }
}
.header__mobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .header__mobile .header__logo,
.header__mobile .header__nav,
.header__mobile .header__phone {
    display: block;
  }
}
.header__close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.footer {
  padding: 67px 0 50px;
  background-color: #252525;
  color: #ffffff;
}
.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.footer__col {
  width: 20%;
  margin-bottom: 20px;
}
@media only screen and (max-width: 1079px) {
  .footer__col {
    width: 50%;
  }
}
@media only screen and (max-width: 560px) {
  .footer__col {
    width: 100%;
    text-align: center;
  }
}
.footer__copyrights, .footer__policy, .footer__created {
  font-size: 14px;
  line-height: 17px;
  color: rgba(156, 156, 156, 0.7);
}
.footer__copyrights {
  margin-top: 20px;
}
.footer__policy, .footer__created {
  display: inline-block;
  text-decoration: underline;
  margin-top: 20px;
}
@media (min-width: 1080px) {
  .footer__policy:hover, .footer__policy:focus, .footer__created:hover, .footer__created:focus {
    text-decoration: none;
  }
}
.footer__contacts a,
.footer address, .footer__schedule {
  font-size: 14px;
  line-height: 180%;
  font-weight: 500;
  font-style: normal;
  color: #ffffff;
}
@media (min-width: 1080px) {
  .footer__contacts a:hover, .footer__contacts a:focus {
    text-decoration: underline;
  }
}

/* Blocks */
.first-screen {
  display: flex;
  height: 100vh;
  position: relative;
  overflow: hidden;
}
@media only screen and (max-width: 1079px) {
  .first-screen {
    overflow: visible;
  }
}
.first-screen::before, .first-screen::after {
  display: flex;
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
}
@media only screen and (max-width: 1079px) {
  .first-screen::before, .first-screen::after {
    display: none;
  }
}
.first-screen::before {
  width: 54%;
  left: 0;
}
.first-screen::after {
  width: 46%;
  right: 0;
  background-color: #01488a;
}
.first-screen__container {
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 1079px) {
  .first-screen__container {
    flex-direction: column;
  }
}
.first-screen__col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: inherit;
  position: relative;
}
.first-screen__col:first-child {
  width: 54%;
  padding: 70px 0 80px;
}
@media only screen and (max-width: 1079px) {
  .first-screen__col:first-child {
    width: 100%;
    height: 40vh;
    padding: 0;
  }
}
@media (max-width: 1079px) and (orientation: landscape) {
  .first-screen__col:first-child {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 70px 0 20px;
  }
}
.first-screen__col:last-child {
  align-items: flex-end;
  width: 46%;
  padding: 70px 0 20px;
  background-color: #01488a;
  z-index: 1;
}
@media only screen and (max-width: 1079px) {
  .first-screen__col:last-child {
    align-items: flex-start;
    width: calc(100% + 48px);
    height: 60vh;
    margin: 0 -24px;
    padding: 20px 0;
    overflow: hidden;
  }
}
.first-screen__title {
  font-size: 46px;
  line-height: 110%;
  font-weight: 500;
  color: #151515;
}
@media (max-width: 1079px) and (orientation: landscape) {
  .first-screen__title {
    font-size: 32px;
    line-height: 40px;
  }
}
@media only screen and (max-width: 1079px) {
  .first-screen__title {
    display: none;
  }
}
@media only screen and (max-width: 767px) {
  .first-screen__title {
    font-size: 32px;
    line-height: 40px;
  }
}
.first-screen__title span {
  color: #01488a;
}
.first-screen__title span.model {
  display: none;
}
.first-screen__title--mobile {
  display: none;
}
@media only screen and (max-width: 1079px) {
  .first-screen__title--mobile {
    display: block;
  }
}
.first-screen__text {
  font-size: 16px;
  line-height: 160%;
  color: #6b6b6b;
  margin-top: 40px;
}
@media only screen and (max-width: 1079px) {
  .first-screen__text {
    display: none;
  }
}
.first-screen__btn {
  max-width: 240px;
  margin-top: 48px;
}
@media only screen and (max-width: 1079px) {
  .first-screen__btn {
    margin-top: 30px;
  }
}
@media (max-width: 1079px) and (orientation: landscape) {
  .first-screen__btn {
    margin-top: 0;
  }
}
@media only screen and (max-width: 1079px) {
  .first-screen__btn a {
    padding: 9px 10px;
    font-size: 14px;
  }
}
.first-screen__nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-right: 41px;
  position: absolute;
  bottom: 40px;
  left: 0;
}
@media only screen and (max-width: 1079px) {
  .first-screen__nav {
    display: none;
  }
}
.first-screen .pagi-mob {
  align-self: center;
  position: relative;
  z-index: 1;
}
.first-screen__anchor {
  animation: bounce 2s linear infinite;
  cursor: pointer;
}
@media (min-width: 1080px) {
  .first-screen__anchor:hover, .first-screen__anchor:focus {
    animation: none;
  }
}
.first-screen__anchor svg {
  fill: #01488a;
